.gallery, .gallery__intro {
    width: 100%;
    height: 100%;
}

.gallery__intro {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: -webkit-gradient(linear, left top, left bottom, from(#fdfbfb), to(#ebedee));
    background: -webkit-linear-gradient(#fdfbfb, #ebedee);
    background: -o-linear-gradient(#fdfbfb, #ebedee);
    background: linear-gradient(#fdfbfb, #ebedee);
}

.gallery__welcome {
    font-size: 24px;
    color: rgb(33, 122, 195);
    -webkit-animation-name: welcome_appear;
            animation-name: welcome_appear;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    margin-bottom: 60px;
}

.gallery__explore {
    font-size: 24px;
    color: rgb(33, 122, 195);
    -webkit-animation-name: explore_appear;
            animation-name: explore_appear;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    cursor: pointer;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transition-duration: 500ms;
         -o-transition-duration: 500ms;
            transition-duration: 500ms;
}

.gallery__explore:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

@-webkit-keyframes welcome_appear {
    from {
        margin-top: 100px;
        opacity: 0;
    }
    to {
        margin-top: 0;
        opacity: 1;
    }
}

@keyframes welcome_appear {
    from {
        margin-top: 100px;
        opacity: 0;
    }
    to {
        margin-top: 0;
        opacity: 1;
    }
}

@-webkit-keyframes explore_appear {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes explore_appear {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.gallery__grid {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-auto-flow: row;
    background: -webkit-gradient(linear, left top, left bottom, from(#fdfbfb), to(#ebedee));
    background: -webkit-linear-gradient(#fdfbfb, #ebedee);
    background: -o-linear-gradient(#fdfbfb, #ebedee);
    background: linear-gradient(#fdfbfb, #ebedee);
}

.gallery__grid__header {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;
    -ms-flex-item-align: stretch;
        align-self: stretch;
}


