.example {
    position: relative;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0;
}

.example__curtain {
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 0;
    -webkit-transition-property: opacity;
    -o-transition-property: opacity;
    transition-property: opacity;
    -webkit-transition-duration: 500ms;
         -o-transition-duration: 500ms;
            transition-duration: 500ms;
    opacity: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    overflow: hidden;
    background: -webkit-gradient(linear, left top, left bottom, from(#fdfbfb), to(#ebedee));
    background: -webkit-linear-gradient(#fdfbfb, #ebedee);
    background: -o-linear-gradient(#fdfbfb, #ebedee);
    background: linear-gradient(#fdfbfb, #ebedee);
    cursor: pointer;
    color: rgb(33, 122, 195);
}

.example:hover .example__curtain {
    opacity: 0.6;
}

.example__curtain__span {
    font-size: 26px;
    font-weight: 800;
}


